import { memo } from "react";
import PokemonType from "../../types/PokemonType";
import ListItem from "../ListItem/ListItem";
import TypeList from "../TypeList/TypeList";

import { Pokemon } from 'pokenode-ts'

import Pagination from 'react-js-pagination';
import './Pagination.css';

import './List.css';

type Props = {
    pokemonList: Pokemon[],
    typesList: PokemonType[] | undefined,
    isLoading: boolean,
    handleSelectType: (type: string) => void,
    handleSelectPokemon: (Pokemon: Pokemon) => void,
    selectedType: string,
    activePage: number,
    handleChangePage: (type: number) => void,
    totalPokemonCount: number,
    countOnPage: number,
    handleSelectCount: (number: number) => void,
    availableCounts: number[]
};

export const List: React.FC<Props> = ({
    pokemonList,
    typesList,
    isLoading,
    handleSelectType,
    handleSelectPokemon,
    selectedType,
    activePage,
    handleChangePage,
    totalPokemonCount,
    countOnPage,
    handleSelectCount,
    availableCounts
}) => {
    return (
        <>
            <TypeList
                types={typesList}
                handleSelectType={handleSelectType}
                selectedType={selectedType}
                countOnPage={countOnPage}
                handleSelectCount={handleSelectCount}
                availableCounts={availableCounts}

            />
            <div className='list'>
                {isLoading
                    ? <div className="error-wrapper">Loading...</div>
                    : (pokemonList.length > 0
                        ? pokemonList.map((pokemon) => (
                            <ListItem
                                key={pokemon.name}
                                pokemon={pokemon}
                                handleSelectPokemon={handleSelectPokemon}
                            />))
                        : <div className="error-wrapper">No Pokemons</div>) }
            </div>
            <div className="pagination-wrapper">
                <Pagination
                    activePage={activePage}
                    totalItemsCount={totalPokemonCount}
                    itemsCountPerPage={countOnPage}
                    pageRangeDisplayed={5}
                    onChange={(page) => handleChangePage(page)}
                    innerClass="pagination"
                    itemClass="page-item"
                    linkClass="page-link"
                    activeLinkClass="active"
                    disabledClass="disabled"
                    prevPageText={'<'}
                    nextPageText={'>'}
                />
            </div>
        </>
    )
};

export default memo(List);